.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 360px) {
  .container {
    display: inline-block;
    width: 360px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    width: 1024px;
  }
}